header.header-section-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: #FFFFFF;
  box-shadow: 0px 100px 80px rgba(182, 187, 245, 0.07), 0px 41.7776px 33.4221px rgba(182, 187, 245, 0.0503198), 0px 22.3363px 17.869px rgba(182, 187, 245, 0.0417275), 0px 12.5216px 10.0172px rgba(182, 187, 245, 0.035), 0px 6.6501px 5.32008px rgba(182, 187, 245, 0.0282725), 0px 2.76726px 2.21381px rgba(182, 187, 245, 0.0196802);
}

header.header-section-wrapper > a.logo-wrapper {
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
}

header.header-section-wrapper > a.logo-wrapper > img.header-logo-image {
  width: 180px;
  height: auto;
}

header.header-section-wrapper > a.logo-wrapper > p {
  font-size: 50px;
  margin-left: -10px;
  font-weight: 700;
  color: #ed2f59;
}

header.header-section-wrapper > div.header-options-wrapper {
  display: flex;
  align-items: center;
  flex-direction: row;
  grid-gap: 20px;
  gap: 20px;
  margin-left: auto;
  margin-right: 30px;
}

header.header-section-wrapper > div.header-options-wrapper > li.header-options-link {
  list-style: none;
}

header.header-section-wrapper > div.header-options-wrapper > li.header-options-link > a {
  text-decoration: none;
  color: black;
  font-weight: 500;
  font-size: inherit;
}

header.header-section-wrapper > div.header-options-wrapper > li.header-options-link > a:hover {
  transition: all 0.2s ease-in-out;
  color: #ed2f59;
  border-bottom: 2px solid #ed2f59;
}

header.header-section-wrapper > div.header-options-wrapper > li.header-options-link > a:active {
  color: #ed2f59;
}
