div.home-section-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

div.home-section-wrapper > div.home-section-content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

div.home-section-wrapper > div.home-section-content-wrapper > p.home-section-title {
  font-family: 'Baloo 2';
  font-weight: 800;
}

div.home-section-wrapper > div.custom-wavy-shape-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

div.home-section-wrapper > div.custom-wavy-shape-bottom > svg {
  position: relative;
  display: block;
  width: calc(143% + 1.3px);
  height: 250px;
}

div.home-section-wrapper > div.custom-wavy-shape-bottom > svg > path.shape-fill {
  fill: #ED2F59;
}
